import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { StyledBackdrop, StyledModalBox } from './UserModal.styled';

const UserModal = ({ onClose }) => {
  const handleBackdropClick = event => {
    if (event.target === event.currentTarget) {
      onClose();
    }
  };
  return (
    <StyledBackdrop onClick={handleBackdropClick}>
      <div className="user-modal__new-box">
        <StyledModalBox>
          <Link to="/register" replace="true" onClick={onClose}>
            <p className="user-modal__new">
               ?
              <span className="user-modal__new-link">  </span>
            </p>
          </Link>
          <p className="user-modal__cabinet"> </p>
          <p className="user-modal__history"> </p>
        </StyledModalBox>
      </div>
    </StyledBackdrop>
  );
};

export default UserModal;

UserModal.propTypes = {
  onClose: PropTypes.func.isRequired,
};
